import { types } from "../types/types";

const initialState = {
  checking: true
}

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.authLogin:
      return {
        ...state,
        ...action.payload,
        checking: false
      }
    case types.authCheckingFinish:
      return {
        ...state,
        checking: false,
      }
    case types.authLogout:
      return {
        checking: true
      }
    default:
      return state;
  }
}

export default authReducer;